// gh-pages, axios, cheerios already installed
import { useEffect, useState } from "react"
import { Box } from "@chakra-ui/react"
import axios from "axios"

import MainTable from "./MainTable"



//       ,   react  : cannot read property of 'undefined',       API  .    ,  react       promise,     data          jsx    .

//      ,      ,       (  useState,   error, setError)/     -  data      (        react,  react    ,    ,       )   .    promise   ,     API.

//     4   (   API):
// 1 -    (data )
// 2 -    (data   )
// 3 -    ( , data   )
// 4 -     data (     data    ,        API,    )

function Parser() {

    const [data, setData] = useState()
    const [loading, setLoading] = useState(true)
    const [error, setError] = useState()

    const url = 'https://randomuser.me/api/?results=5'

    useEffect(() => {
        axios.get(url)
             .then((response) => {
                setData(response.data.results)
                setLoading(false) 
             })
             .catch(error => setError(error) )
    },[])

 console.log(data)

    if (loading) return (<h1>Loading in progress...</h1>)

    if (error) return (<h1>Error</h1>)

   
    return(
        <Box>
            <MainTable data = {data} />
        </Box>
    )

    





}
export default Parser







