import React from "react";

import { useState } from "react";


import {
    Table,
    Thead,
    Tbody,
    Tr,
    Th,
    Td,
    TableCaption,
    TableContainer,
    extendTheme, 
    useBreakpointValue
  } from '@chakra-ui/react'

  // default values for breakpoints chakra ui
 
  const breakpoints = { 
    sm: '320px',
    md: '768px',
    lg: '1100px',
    xl: '1200px',
    '2xl': '1536px'
  }

  // eslint-disable-next-line no-unused-vars
  const theme = extendTheme({breakpoints})


export default function MainTable (props) {

    const [selectedRow, setSelectedRow] = useState(null)

    const isSmallerScreen = useBreakpointValue({base: true, sm: true, md: true, lg: false, xl: false})


    function MakeTableData (props) {
        
        const selectPerson = (obj, number) => {
          return (
            obj[number]
          )
        }

        //      onClick (  hook useState -         key  ),      ""   (   background -  ).  style {} '  ,         . 

        // key      selectedrow   ( background)

        const MakeRow = (props) => {
          return (
            <Tr key={props.personalData.name.first} style = {{
              background: selectedRow === props.personalData.name.first ? 'orange' : 'transparent',
              cursor: 'pointer'
              
          }}
          onMouseEnter={() => setSelectedRow(props.personalData.name.first)} fontSize={{sm: '20px', lg: '21px', xl: '26px'}}>
              <Td>{props.personalData.gender}</Td>
              <Td>{props.personalData.name.first}</Td>
              <Td>{props.personalData.name.last}</Td>
              <Td>{props.personalData.cell}</Td>
              {isSmallerScreen ? null : <Td>{props.personalData.email}</Td>}
              <Td>{props.personalData.dob.age}</Td>
          </Tr>
          )
        }

        const arrayLength = props.data.length

        return (
          
          
            Array(arrayLength).fill().map((_, index) => (
              <MakeRow key = {index} personalData = {selectPerson(props.data, index)} />
            ) )
          
        )

        //  return         (    )   .            .fill() (      fill,      undefined,       )    .    map  s    .
    }



// main return 

    return (
      
        <TableContainer >
  <Table>
    <TableCaption>People information</TableCaption>
    <Thead>
      <Tr>
        <Th>Gender</Th>
        <Th>First Name</Th>
        <Th>Last Name</Th>
        <Th>Cell</Th>
        {isSmallerScreen ? null : <Th>Email</Th>}
        <Th>Age</Th>
      </Tr>
    </Thead>
    <Tbody>
      <MakeTableData data = {props.data} />
    </Tbody>
    
  </Table>
</TableContainer>
      
    )
}

